.ring {
  position: fixed;
  top: 0;
  left: 0;
  width: 22px;
  height: 22px;
  border: 2px solid rgba(31, 30, 30, 0.808);
  border-radius: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition-duration: 1ms;
  transition-timing-function: ease-out;
  will-change: width, height, transform, border;
  z-index: 2;
  pointer-events: none;
}

  
