.contact {
	width: 100%;
	height: 84.5vh;
	display: grid;
	align-items: center;
	grid-template-columns: 10% 90%;
}

.contact div h1 {
	font-size: 4.5rem;
	font-weight:  700;
	margin-bottom: 2%;
}

.message {
	width: 60%;
	margin-bottom: 5%;
	font-size: 1.4rem;
	font-weight: 100;
}


.buttonFill {
	width: 50px;
	height: 50px;
	display: grid;
	border-radius: 50px;
	align-items: center;
	background-color: #EAEAEB;
	white-space: nowrap;
	cursor: pointer;
}


.button {
	text-transform: uppercase;
	font-weight: 600;
	font-size:  1.1rem;
	margin: 0% 14%;
	display: inline-block;
}

.header {
	writing-mode: vertical-rl;
	font-size: 0.9rem;
	transform: scale(-1, -1);;
}

@media screen and (max-width: 1367px) {
	.message {
		width: 80%;
	}
}

@media screen and (max-width: 500px) {
	.message {
		width: 80%;
		font-size: 1.1rem;
	}

	.header {
		display: none;
	}

	.contact div h1 {
		font-size: 2.5rem;
		margin-bottom: 10%;
	}
}